<script>

import about from '@/views/Layout/components/about'
import ipcRenderer from '@/qt-ipc/Ipc-renderer'
import vue from 'vue'

vue.use(about)
export default {
    data() {
        return {
            showAboutUsDialog: false,
            aboutCallBackRef: null,
            aboutEl: null
        }
    },
    mounted() {
        this.aboutEl = this.$aboutUsDialog(document.getElementById('app'), { value: this.showAboutUsDialog })
        this.aboutEl.$on('input', (val) => {
            this.showAboutUsDialog = val
            this.aboutEl.$props.value = val
        })
        this.aboutCallBackRef = () => {
            this.showAboutUsDialog = true
            this.aboutEl.$props.value = this.showAboutUsDialog
        }
        ipcRenderer.on('qt_show_about', this.aboutCallBackRef)
    },
    beforeDestroy() {
        ipcRenderer.off('qt_show_about', this.aboutCallBackRef)
        this.aboutEl.$off('input')
    }
}
</script>
